import React from 'react'
import {
    MainContainer,
    MainWrapper,
    MainSpeakerWrapper,
    MainSpeakerImg,
    MainSpeakerInfo,
    MainSpeakerH1,
    MainSpeakerDesc,
} from './HomeMainElements'
import ProductList from '../../ProductList/'
import speakerZX9Img from '../../../assets/home/desktop/image-speaker-zx9.png'
import { GlobalBtnBlack } from '../../../GlobalBtn'


const HomeMain = () => {
    return (
        <MainContainer>
            <MainWrapper>
                <ProductList />
                <MainSpeakerWrapper>
                    <MainSpeakerImg  data-aos="fade-up" data-aos-duration="1000" bg={speakerZX9Img} aria-label="popular speaker product image" />
                    <MainSpeakerInfo>
                        <MainSpeakerH1>ZX9 SPEAKER</MainSpeakerH1>
                        <MainSpeakerDesc>
                            Upgrade to premium speakers that are phenomenally built 
                            to deliver truly remarkable sound.
                        </MainSpeakerDesc>
                        <GlobalBtnBlack data-text="see product" />
                    </MainSpeakerInfo>
                </MainSpeakerWrapper>
            </MainWrapper>
        </MainContainer>
    )
}

export default HomeMain
