import React from 'react'
import{
    HeroContainer,
    HeroWrapper,
    HeroDesc,
    HeroTitle,
    HeroProductName,
    HeroInfo,
    HeroImgWrapper,
    HeroImg
} from './HeroElements'
import {GlobalBtnBrown} from '../../GlobalBtn'
const Hero = () => {
    return (
        <HeroContainer>
            <HeroWrapper>
                <HeroDesc>
                    <HeroTitle data-aos="fade-down" data-aos-duration="500">new product</HeroTitle>
                    <HeroProductName data-aos="fade-right" data-aos-duration="1100" data-aos-delay="650">
                        XX99 Mark II Headphones
                    </HeroProductName>
                    <HeroInfo data-aos="zoom-in" data-aos-delay="1400" data-aos-duration="1100">
                        Experience natural, lifelike audio and exceptional 
                        build quality made for the passionate music enthusiast.
                    </HeroInfo>
                    <GlobalBtnBrown 
                        data-aos="zoom-in-up" 
                        data-aos-delay="1900" 
                        data-aos-duration="700" 
                        data-text="see product" 
                        aria-label="link to product page"
                    />
                </HeroDesc>
                <HeroImgWrapper aria-label="giant headphone image on background">
                    <HeroImg  />
                </HeroImgWrapper>
            </HeroWrapper>
        </HeroContainer>
    )
}

export default Hero
