import Styled from 'styled-components'
import { SPACE, WIDTH, COLORS, FONT } from '../../../Constants'


export const MainContainer=Styled.div`
    display:flex;
    justify-content:center;
    padding:${SPACE.container};
    margin:20rem 0;
`
export const MainWrapper = Styled.div`
    max-width:${WIDTH.desktop};
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
`
export const MainSpeakerWrapper = Styled.div`
    width:100%;
    min-height:56rem;
    display:flex;
    border-radius:8px;
    background:${COLORS.brownDark};
    padding:9.5rem;
    position:relative;
    justify-content:flex-end;
    align-items:center;
    overflow:hidden;
`
export const MainSpeakerImg = Styled.div`
    z-index:1;
    position:absolute;
    background:url(${props => props.bg});
    background-repeat:no-repeat;
    background-size:contain;
    left:9.5rem;bottom:-11rem;
    max-width:38rem;
    width:100%;
    height:100%;
`
export const MainSpeakerInfo = Styled.div`
    z-index:2;
    max-width:34.9rem;
`
export const MainSpeakerH1 = Styled.div`
    ${FONT.mainProductTitle};
    color:${COLORS.white};
    margin-bottom:2.4rem;
`
export const MainSpeakerDesc = Styled.p`
    opacity:.75;
    font-weight:100;
    font-size:1.5rem;
    color:${COLORS.white};
    margin-bottom:6.5rem;
`

